@import "../../../style/_global"

.practice
    padding: 30px 0 80px

    .row
        gap: 88px
        align-items: center

        &:last-child
            gap: 52px
            .practice_right_img
                width: 536px
                height: 380px
                span
                    left: -88px
                    bottom: -69px

        .practice_left_info
            width: 500px
            h3
                @include blackText()
                font-weight: 700
                display: flex
                align-items: center
                gap: 12px
                margin-bottom: 15px

            .left_info_text
                @include blackText()
                &:last-child
                    margin-top: 15px

        .practice_right_img
            width: 558px
            height: 324px
            position: relative

            img
                width: 100%
            span
                position: absolute
                right: -19px
                bottom: -49px
                font-style: normal
                font-weight: 900
                font-size: 105.429px
                line-height: 130%
                color: $colorNumber

//  media query
@media (max-width: 1300px)
.practice
    .row
        gap: 20px

@media (max-width: 1199px)
    .practice
        .row
            .practice_left_info
                width: 370px

        .row:last-child
            gap: 30px
            .practice_right_img span
                left: 0

@media (max-width: 991px)
    .practice
        padding-top: 40px
        .row
            &:first-child
                margin-bottom: 40px
            .practice_left_info, .practice_right_img
                width: 340px
                height: auto
            .practice_right_img
                span
                    display: none
            &:last-child
                gap: 20px
                .practice_left_info, .practice_right_img
                    width: 340px
                    height: auto
                .practice_right_img
                    span
                        display: none

@media (max-width: 767px)
    .practice
        padding-top: 40px
        padding-bottom: 30px
        .row
            .practice_left_info, .practice_right_img
                width: 100%
            &:last-child
                flex-direction: column-reverse
                .practice_left_info, .practice_right_img
                    width: 100%
